.banner-3 {
  background: url("../../assets/images/home/banner3.jpg");
  -webkit-background-size: cover;
  background-size: cover;
  position: relative;
}

.banner-content {
  margin: auto;
}

.content-padding {
  padding: 180px 0px 180px;
}

.banner-content .banner-title {
  color: #ffffff;
  font-weight: 300;
  /* line-height: 50px; */
}

.main-banner {
  font-size: 8vh;
}

/* About EEE */
#intro {
  padding-bottom: 90px;
}

.section-padding {
  padding: 90px 0px;
}

.section-heading {
  text-align: center;
  width: 70%;
  margin: 0 auto 60px;
}

.section-title {
  font-size: 32px;
  line-height: 38px;
  margin-bottom: 15px;
  text-transform: capitalize;
}

/* .intro-img {
  margin-top: 45px;
} */

.intro-box {
  margin-top: 30px;
  position: relative;
  margin-bottom: 20px;
}

/* Members List */
#about {
  position: relative;
  /* padding-bottom: 85px; */
  padding-top: 85px;
}

.subtitle {
  color: #635cdb;
}

/* Mentors */
.bg-feature {
  background-color: #e8f2ff;
}

#service-head {
  padding: 40px 0;
}

#service-head .section-title {
  color: #fff;
}

.service-inner {
  margin-top: 30px;
}

.service-inner h4 {
  margin-bottom: 20px;
}

#blog {
  padding-bottom: 55px;
}
.blog-block {
  overflow: hidden;
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}

.blog-block img {
  margin-bottom: 10px;
  border-radius: 300px;
}

.blog-text {
  padding: 20px;
}

a {
  text-decoration: none;
  -webkit-transition: all 0.4s;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  color: #000;
}

a:hover {
  color: #635cdb;
  text-decoration: none;
}

a:focus {
  outline: none;
  text-decoration: none;
}

/* Media queries */
@media (max-width: 575.98px) {
  .banner-content .banner-title {
    line-height: 37px;
    font-size: 5vh;
  }

  .banner-content p {
    font-size: 16px;
  }

  /* #intro {
    padding-bottom: 90px;
  } */
  .section-heading {
    width: 100%;
  }
  .section-title {
    font-size: 26px;
    line-height: 36px;
  }
  .intro-box {
    margin: -70px auto 40px auto;
  }
}
