#header-gallery
{
    height: 60px;
    width: 100%;
    background-color: #BDBDBD;
    /*font-family: 'Poiret one', cursive;*/
    font-size: 20px;
    position: fixed;
    z-index: 999;
    border-radius: 3px;
    left: 50%;
    transform: translateX(-50%);
    }
#header-gallery a
{
    text-decoration: none;
    color: black;
    float: right;
    margin-top: 15px;
    margin-left: 50px;
    margin-right: 30px;
    font-weight: bold;
}
#header-gallery img
{
    
    height:50px;
    width: 70px;
    float: left;
}
#header-gallery a:hover
{
    transform: scale(1.4,1.4);
    text-decoration: overline;
    color: white;
}



.container-gallery
{
    height: 768px;
    width: 100%;
    position: absolute;
    top: 110px;
    display: inline-block;
    text-align: center;
}

.small-gallery
{
    height: 250px;
    width: 350px;
    position: relative;
    margin: 3px;
    border-radius: 5px;
    padding: 5px;
    
    }
.small-gallery:hover
{
    transform: scale(1.03, 1.03);
    
    
}
.lightbox-gallery
{

    display: none;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.7);
    
}

