.banner-content {
  padding-right: 30px;
}

#page-banner {
  position: relative;
  padding: 60px 0px 80px;
}

.content-padding {
  padding: 180px 0px 80px;
}

#workshop-service {
  padding: 20px;
}

.service-box {
  margin-bottom: 40px;
  background: #fff;
  text-align: center;
  padding: 45px 25px 30px 25px;
  border: 1px solid transparent;
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  border-radius: 5px;
  -webkit-box-shadow: 0px 3px 7px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 3px 7px rgba(0, 0, 0, 0.2);
}

.service-inner {
  margin-top: 30px;
}

.service-inner h4 {
  margin-bottom: 20px;
}

/* C-programming Workshop */
.section-padding {
  padding: 50px 10px;
  margin: 0 auto;
}

/* Horizantal line */
.line-big {
  width: 90%;
  background: rgb(203, 201, 255);
  height: 1px;
  margin: auto;
}

@media (max-width: 575.98px) {
  h1 {
    font-size: 32px;
    line-height: 36px;
  }
}
