.banner-content {
  padding-right: 30px;
}
.banner-content p {
  margin: 20px 0px;
  color: #000;
  font-size: 35px;
  line-height: 30px;
}

#service {
  margin-top: -100px;
  padding-bottom: 70px;
}

.content-padding {
  padding: 180px 0px 80px;
}

#page-banner {
  position: relative;
  padding: 60px 0px 80px;
}

.overlay {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  /* background: rgba(71, 80, 111,0.7); */
}

.service-box {
  margin-bottom: 40px;
  background: #fff;
  text-align: center;
  margin: auto;
  padding: 45px 25px 30px 25px;
  border: 1px solid transparent;
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  border-radius: 5px;
  -webkit-box-shadow: 0px 3px 7px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 3px 7px rgba(0, 0, 0, 0.2);
}

.line {
  width: 50px;
  background: #635cdb;
  height: 1px;
  margin: 10px auto;
}

.btn {
  display: inline-block;
  padding: 15px 45px 15px 45px;
  text-transform: uppercase;
  border: 2px solid transparent;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  border-radius: 0px;
  font-weight: 600;
  font-size: 13px;
  letter-spacing: 1px;
}

.btn:focus,
.btn:active,
.btn:active:focus {
  outline: none;
  box-shadow: none;
}

.btn-sm {
  padding: 10px 30px;
}
